@import url(https://fonts.googleapis.com/css2?family=Creepster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background-image: url(/-breakingbad/static/media/bb.c3fc96f5.jpg);
    background-size: 100%;
    background-repeat: repeat;
}
.image {
  display: flex;
  /* margin-top: 350px; */
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  top: 25%;
  
}
.characterImage {
  margin: 10px;
  width: 300px;
  height: 600px;
  border-radius: 10px;
  justify-content: center;
}
figure {
    display: flex;
    align-items: center;
    justify-content: center;
  width: 250px;
  height: 450px;
  overflow: hidden;
  margin: 10px;
  position: relative;
  
}

figcaption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1000%;
    color: green;
    opacity: 0;
    padding-top: 3rem;
}
figure:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-color: transparent;
  }
  figcaption p {
    margin-left: 2rem;
  }
  figure:hover figcaption {
    opacity: 1;
    background: rgb(0, 0, 0, 0.9);
    transition: all 1s linear;
  }
.input{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20vh;
    /* margin-bottom: 0; */
}
input{
    width: 50%;
    border-color: blue;
    border-radius: 6px;
}
input:active{
    outline: 2px solid green;
}
.logo{
    display: flex;
    justify-content: center;align-items: center;
    width: 100vw;
}
h1{
    display: flex;;
    justify-content: center;
    color: darkgreen;
    font-family: 'Creepster', cursive;
    font-size: 4rem;
}
